* {
  padding: 0px;
  margin: 0px;
  -webkit-font-smoothing: antialiased;
}

.Screen {
  background-color: #f0f0f0;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h2 {
  font-size: 20px;
  font-weight: 500;
  color: rgb(140, 140, 140);
}

h1 {
  font-weight: 800;
  width: 80%;
  text-align: center;
  word-break: keep-all;
}

h3 {
  font-size: 60px;
  margin-bottom: 20px;
}

input {
  margin-top: 20px;
  width: 80%;
  max-width: 500px;
  height: 50px;
  border-radius: 10px;
  border: 0.1px solid rgb(240, 240, 240);
  background-color: white;
  box-shadow: 0px 3px 15px -8px #fff;
  text-align: center;
  font-weight: 400;
  color: rgb(25, 25, 25);
  font-size: 15px;
}

.Recent {
  width: 80%;
  background-color: white;
  max-width: 500px;
  margin-top: 20px;
  border-radius: 10px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
}

.Item {
  width: 100%;
  height: 140px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.1s;
}

.Item:hover {
  background-color: rgb(250, 250, 250);
}

h5 {
  font-weight: 500;
  color: rgb(50, 50, 50);
  width: 50px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  text-align: center;
}

.Circle {
  width: 50px;
  height: 50px;
  background-color: rgb(240, 240, 240);
  border-radius: 100%;
  text-align: center;
  line-height: 50px;
  font-weight: 900;
  font-size: 25px;
}

h6 {
  width: 80%;
  max-width: 500px;  
  text-align: right;
  margin-top: 10px;
  font-weight: 300;
  cursor: pointer;
  color: red;
}

button {
  margin-top: 20px;
  width: 80%;
  max-width: 500px;
  height: 50px;
  border: 0px;
  background-color: #33e3b1;
  border-radius: 10px;
  font-weight: 900;
  font-size: 20px;
  color: white;
  cursor: pointer;
  transition: 0.3s;
}

button:hover {
  box-shadow: 0px 0px 15px -6px #33e3b1;
}